#ui-datepicker-div {
  display: none;
  margin-top: 10px;
  width: 320px;
  padding: 10px 30px 20px;
  border-radius: 15px;
  background-color: #ffffff;
  box-shadow: 0 15px 35px 0 rgba(19, 192, 215, 0.35);
}

.datepicker__main {
  width: 320px;
  position: relative;
}

.datepicker__input {
  width: 100%;
}

.ui-datepicker-trigger {
  position: absolute;
  right: 5px;
  top: 2px;
}

.ui-datepicker-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ui-datepicker-header .ui-datepicker-prev, .ui-datepicker-header .ui-datepicker-next {
  cursor: pointer;
  user-select: none;
  color: lightseagreen;
}

.ui-datepicker-header .ui-datepicker-next {
  order: 3;
}

.ui-datepicker-header .ui-datepicker-next.ui-state-disabled {
  pointer-events: none;
  color: #cac8ce;
}

.ui-datepicker-header .ui-datepicker-title {
  cursor: pointer;
  user-select: none;
}

.ui-datepicker-header .ui-datepicker-title:after {
  display: inline-block;
  content: '>';
  margin-left: 5px;
  color: lightseagreen;
  transform: rotate(90deg);
}

.ui-datepicker-calendar {
  width: 100%;
  text-align: center;
}

.ui-datepicker-calendar thead {
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: 3px;
  text-align: center;
  color: #a4a0ad;
}

.ui-datepicker-calendar th, .ui-datepicker-calendar td {
  padding: 0;
  margin: 0;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  user-select: none;
}

.ui-datepicker-calendar th {
  font-size: 12px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: 3px;
  text-align: center;
  color: #a4a0ad;
}

.ui-datepicker-calendar td {
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: 0.5px;
  text-align: center;
  color: #322c48;
}

.ui-datepicker-calendar td.ui-state-disabled {
  color: #a4a0ad;
  cursor: not-allowed;
}

.ui-datepicker-calendar td a {
  text-decoration: none;
  color: #322c48;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50px;
}

.ui-datepicker-calendar td a:hover {
  background-color: #f1f0f2;
  color: #13c0d7;
}

.ui-datepicker-calendar .ui-state-active {
  background-color: #13c0d7;
  color: white;
}

.datepicker__edit {
  display: flex;
  display: none;
  margin-top: 10px;
  width: 320px;
  height: 370px;
  overflow: hidden;
  padding: 20px 30px 20px;
  border-radius: 15px;
  background-color: #ffffff;
  box-shadow: 0 15px 35px 0 rgba(19, 192, 215, 0.35);
}

.datepicker__edit .datepicker__edit_container {
  display: flex;
  align-items: flex-start;
}

.datepicker__edit_header {
  position: relative;
  width: 100%;
  height: 30px;
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: 0.5px;
  color: #322c48;
  margin-bottom: 20px;
  cursor: pointer;
  user-select: none;
}

.datepicker__edit_close {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../images/icon/close.svg");
  cursor: pointer;
}

.datepicker__edit_close:hover {
  background-image: url("../images/icon/close_hover.svg");
}

.datepicker__edit_month {
  display: flex;
  width: 66.6%;
  align-items: flex-start;
  flex-wrap: wrap;
  border-right: 1px solid #f1f0f2;
}

.datepicker__edit_month_item {
  width: 50%;
}

.datepicker__edit_month_item:nth-last-child(1), .datepicker__edit_month_item:nth-last-child(2) {
  margin-bottom: 0px;
}

.datepicker__edit_years_item, .datepicker__edit_month_item {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: 0.5px;
  color: #322c48;
  cursor: pointer;
  margin-bottom: 20px;
}

.datepicker__edit_years_item:hover, .datepicker__edit_month_item:hover {
  color: #13c0d7;
}

.datepicker__edit_years_item.checked, .datepicker__edit_month_item.checked {
  color: #13c0d7;
}

.datepicker__edit_years_item.checked:after, .datepicker__edit_month_item.checked:after {
  content: '';
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../images/icon/checkmark.svg");
  background-size: 100%;
}

.datepicker__edit_month_item.checked:after {
  right: 20px;
}

.datepicker__edit_years {
  width: 33%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
  height: 100%;
}

.datepicker__edit_years_item {
  width: 100%;
  padding-left: 20px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
